.carousel-container {
  background-color: yellow;
  margin-top: 20px;
  margin-bottom: 20px;
  overflow: hidden;
  position: relative;
  height: 100%;
  width: 200px;
}

.carousel-card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  flex: 0 0 20%; //acho icono?
}

.carousel-card > img {
  height: 100%;
  width: 100%;
  margin-right: 10px;
}

.carousel-track {
  display: flex;
  position: relative;
  //left: 0;
  justify-content: right;
  align-items: center;
  //gap: 16x; //aumentar el gap segun las pantalla
  width: 200%;
  height: 100%;

  animation: carousel 10s infinite linear;
}

@keyframes carousel {
  0% {
    transform: translateX(100%); //diminuir el % segun la pantalla
  }
  100% {
    transform: translateX(-100%);
  }
}
